.fv {
  position: relative;
  margin-top: rem(80);
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  @include mq(sp) {
    margin-top: rem(55);
  } 
  &__body {
    position: absolute;
    left: rem(70);
    top: 50%;
    transform: translateY(-50%);
    @include mq(tab) {
      left: 50%;
      transform: translateX(-45%);
      top: rem(10);
    }
    @include mq(sp) {
      transform: translateX(-50%);
    }
    @include mq(pc) {
      left: rem(140);
    } 
  }
  &__catch {
    font-size: rem(20);
    line-height: ( 52 / 30 );
    font-weight: bold;
    letter-spacing: 0.05em;
    @include mq(sp) {
      font-size: rem(13);
    }
    @include mq(pc) {
      font-size: rem(26);
    }
  }
  &__title {
    margin-top: rem(40);
    width: rem(350);
    height: auto;
    img {
      width: 100%;
      height: 100%;
    }
    @include mq(sp) {
      margin-top: rem(25);
      width: rem(280);
    }
    @include mq(pc) {
      width: rem(420);
    } 
  }
  &__btnWrap {
    margin-top: rem(65);
    display: flex;
    @include mq(sp) {
      margin-top: rem(40);
    }
  }
  &__btn {
    font-size: rem(15);
    font-weight: bold;
    padding-right: rem(10);
    padding-top: rem(12);
    padding-bottom: rem(12);
    &--left {
      border: rem(3) solid #070749;
      background-color: #fff;
      margin-right: rem(5);
      padding-left: rem(15);
    }
    &--right {
      border: rem(3) solid #070749;
      background-color: #070749;
      color: #fff;
      padding-left: rem(10);
    }
    @include mq(sp) {
      font-size: rem(12);
      padding-top: rem(10);
      padding-bottom: rem(10);
      padding-right: rem(8);
    }
    @include mq(pc) {
      font-size: rem(17);
      padding-top: rem(14);
      padding-bottom: rem(14);
      padding-right: rem(11);
    } 
  }
  &__mainImage {
    width: 69.25%;
    height: auto;
    margin-right: 0;
    margin-left: auto;
    img {
      border-radius: rem(20) 0 0 rem(20);
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top;
      @include mq(tab) {
        border-radius: 0;
      }
    }
    @include mq(tab) {
      width: 100%;
      padding-top: rem(300);
    }
    @include mq(sp) {
      padding-top: rem(220);
    }
    @include mq(pc) {
      width: 60%;
    } 
  }
}